<template>
  <div class="annotation-item-container">
    <div class="flex-space-between">
      <AnnotationUser :user="annotation.modifierInfo" :time="annotation.gmtModifyTime"/>
      <div class="tools">
        <slot name="tools" :item="annotation"/>
      </div>
    </div>
    <p
      class="annotation-message scrollable-y"
      :class="{muted: !annotation.content}"
    >
    <span class="img" v-if="hasImage">{{
        $t('model.comment.img')}}</span>
    {{
    annotation.content ? annotation.content : hasImage ? '' : $t('model.detail.annotation.no_content')
    }}</p>
  </div>
</template>

<script>
import AnnotationUser from './AnnotationUser.vue';


export default {
  components: {
    AnnotationUser,
  },
  computed: {
    /** @return { Boolean } */
    hasImage() {
      const { annotation } = this;
      return annotation.accessoryInfo
      && annotation.accessoryInfo.images
      && annotation.accessoryInfo.images.length;
    },
  },
  name: 'AnnotationItem',
  props: {
    annotation: {
      type: Object,
      required: true,
    },
    disableDelete: {
      type: Boolean,
      default: false,
    },
  },
};
</script>

