

import {
  defineComponent, ref, onMounted, watch,
} from '@vue/composition-api';
import {
  PaperScope,
  Color,
  Path,
} from 'paper';
import { i18n } from '@/i18n';
import { O } from 'ts-toolbelt';
import { ElInput } from 'element-ui/types/input';
import Vue from 'vue';
import useClosest from '_/composables/use-closest';
import Bezier from '@/utils/paper-tools/Bezier';
import BaseTool from '@/utils/paper-tools/BaseTool';
import { Size } from 'paper/dist/paper-core';
import AnnotationDetail from './AnnotationDetail.vue';
import { Sign } from './type.d';

type SignWithPosition = Sign & {
  position: {
    x: number,
    y: number,
  }
};

type PathType = InstanceType<typeof Path>;
type PaperScopeType = InstanceType<typeof PaperScope>;
type BaseToolWithParams =
  InstanceType<typeof BaseTool> & { params: O.Record<string, any>, path?: PathType | null; };

class Comment extends BaseTool {
  params: Object;
  constructor(scope: PaperScopeType) {
    super(scope);
    this.params = {};
  }
}

const AnnotationCreation = defineComponent({
  props: {
    sign: {
      required: true,
      type: Object as () => SignWithPosition,
    },
    readonly: {
      default: false,
      type: Boolean,
    },
  },

  setup(props, { emit }) {
    const paper = new PaperScope();
    const {
      Layer,
    } = paper;

    const renderer = useClosest<any>('Renderer');
    const canvas = ref<HTMLCanvasElement>();
    const contentShown = ref(!!props.readonly);
    const textarea = ref<ElInput>();
    paper.install({});

    const tools = ref<{ [k: string]: BaseToolWithParams }>();
    const tool = ref<BaseToolWithParams>();


    const switchTool = async (_tool: BaseToolWithParams) => {
      const isCommand = _tool instanceof Comment;
      if (tool.value !== _tool) {
        contentShown.value = isCommand;
        tool.value = _tool;
        const d4 = renderer.value?.d4;
        if (d4) {
          if (isCommand) {
            try {
              d4.renderSigns();
            } catch (e) {
              // TODO: 
            }
          } else {
            d4.hideSigns();
          }
        }
        paper.tool = _tool;
      }
    };

    const content = ref('');
    const strokeWidth = ref(5);
    const strokeColor = ref('red');
    type Size = { width: number, height: number };
    const size = ref<Size>();

    function resize(
      { width: newWidth, height: newHeight }: Size,
      { width: oldWidth, height: oldHeight }: Size,
    ) {
      const { Point } = paper;
      const ratio = newHeight / oldHeight;
      paper.project.layers.forEach(item => {
        item.translate(new Point(
          (newWidth - oldWidth) / 2,
          (newHeight - oldHeight) / 2,
        ));
        item.scale(ratio, new Point(newWidth / 2, newHeight / 2));
      });
    }

    onMounted(() => {
      const vCanvas = canvas.value!;
      paper.setup(vCanvas);
      size.value = { width: vCanvas.clientWidth, height: vCanvas.clientHeight };
      const layer = new Layer();
      layer.activate();

      tools.value = {
        comment: new Comment(paper),
        bezier: new Bezier(paper, {
          strokeWidth: strokeWidth.value,
          strokeColor: new Color(strokeColor.value),
        }),
      };
      switchTool(tools.value.comment);

      watch(() => {
        return props.sign.content;
      }, (_content: string) => {
        content.value = _content;
      }, {
        immediate: true,
      });

      watch(strokeWidth, (w) => {
        if (tool.value) {
          tool.value.params.strokeWidth = w;
        }
      }, {
        immediate: true,
      });
      watch(strokeColor, (c) => {
        if (tool.value) {
          tool.value.params.strokeColor = new Color(c);
        }
      }, {
        immediate: true,
      });
      watch(() => {
        return props.sign.brushJson;
      }, (_brushJson, _oBrushJson) => {
        if (_brushJson !== _oBrushJson) {
          const newSize = { width: canvas.value!.clientWidth, height: canvas.value!.clientHeight };
          const j = JSON.parse(_brushJson);
          const d = Array.isArray(j) ? { ...newSize, data: j } : j;
          paper.project.importJSON(JSON.stringify(d.data));

          resize(newSize, d);
        }
      }, {
        immediate: true,
      });
      watch(size, (_size, _oldSize) => {
        if (_size && _oldSize
          && (_size!.width !== _oldSize.width || _size!.height !== _oldSize.height)) {
          paper.project.view.size.width = _size.width;
          paper.project.view.size.height = _size.height;
          paper.project.view.viewSize.width = _size.width;
          paper.project.view.viewSize.height = _size.height;
          resize(_size, _oldSize);
        }
      });
    });


    return {
      resize() {
        size.value = { width: canvas.value!.clientWidth, height: canvas.value!.clientHeight };
      },
      paper,
      content,
      contentShown,
      tool,
      canvas,
      tools,
      textarea,
      switchTool,
      strokeWidth,
      strokeColor,
      val: ref({
        content: '',
        imgs: [
          // eslint-disable-next-line max-len
          { url: 'http://dev.4dshoetech.local/datapipe/repository/userImg/1183939321070227457-7659f198cde5ba9d6600ed4e788fe11e.png?token=1183939321070227457:web:fb2f2654785cc3cdd300fa5b5866eb6b' },
        ] as any[],
        ats: [] as any[],
      }),
      undo() {
        const vTool = tool.value;
        if (vTool) {
          vTool.undo();
        }
      },
      setStrokeWidth(v: number) {
        strokeWidth.value = v;
        if (tool.value && tool.value.path) {
          tool.value.path.strokeWidth = v;
        }
      },
      setStrokeColor(c: string) {
        strokeColor.value = c;
        if (tool.value && tool.value.path) {
          tool.value.path.strokeColor = new Color(c);
        }
      },
      toggleComment() {
        contentShown.value = !contentShown.value;
      },
      submit() {
        if (!content.value.trim() && !paper.project.getItems({
          class: paper.Path,
        }).length && !props.sign.accessoryInfo.images.length) { throw new Error(i18n.t('model.detail.annotation_empty_tip')); }
        paper.project.deselectAll();
        emit('submit', {
          ...props.sign,
          content: content.value,
          brushJson: JSON.stringify({
            width: canvas.value!.clientWidth,
            height: canvas.value!.clientHeight,
            data: JSON.parse(paper.project.exportJSON()),
          }),
        });
        if (tool.value) {
          tool.value.clearHistory();
        }
      },
      close() {
        emit('close');
        if (tool.value) {
          tool.value.clearHistory();
        }
      },
    };
  },
  render() {
    const self = this;
    const {
      tools, tool, switchTool, contentShown, sign, undo,
      submit, close, setStrokeWidth, setStrokeColor, strokeWidth, strokeColor, toggleComment,
      readonly,
    } = self;
    const pointSize = { width: 24, height: 24 };
    let panel;
    let confirmPanel;

    const isEdit = readonly;

    if (!isEdit) {
      panel = <div class="panel">
        {
          !tools || tools.comment === tool ? null

            : <div class="params">
              <a class={['btn-dot-sm', { active: strokeWidth === 2 }]} onClick={() => setStrokeWidth(2)} />
              <a class={['btn-dot-md', { active: strokeWidth === 5 }]} onClick={() => setStrokeWidth(5)} />
              <a class={['btn-dot-lg', { active: strokeWidth === 8 }]} onClick={() => setStrokeWidth(8)} />
              <span class="sep" />
              <a class={['btn-color-red', { active: strokeColor === 'red' }]} onClick={() => setStrokeColor('red')} />
              <a class={['btn-color-white', { active: strokeColor === 'white' }]} onClick={() => setStrokeColor('white')} />
              <a class={['btn-color-black', { active: strokeColor === 'black' }]} onClick={() => setStrokeColor('black')} />
            </div>
        }
        {tools ? <div class="tools">
          <a class={['iconfont iconpinglun btn-comment', { active: tools.comment === tool }]} onClick={() => switchTool(tools.comment)} />
          <span class="sep" />
          <a class={['iconfont icongangbi btn-pen', { active: tools.bezier === tool }]} onClick={() => switchTool(tools.bezier)} />
          <span class="sep" />
          <a class="iconfont iconhoutui btn-undo" onClick={undo} />
        </div> : null}

      </div>;

      confirmPanel = <div class="confirm-panel">
        <el-button size="small" round icon="iconfont iconqueren" onClick={() => self.$act(submit)}></el-button>
        <el-button size="small" round icon="iconfont iconguanbi" onClick={close}></el-button>
      </div>;
    }

    return <div class="annotation-creation" style={{ pointerEvents: isEdit ? 'none' : 'auto' }}>
      <canvas ref="canvas" />
      <div class="comment-layer" style={{ zIndex: contentShown ? 2 : 0 }}>
        <div
          onClick={() => toggleComment()}
          style={{
            pointerEvents: isEdit ? 'all' : 'none',
            width: `${pointSize.width}px`,
            height: `${pointSize.height}px`,
            position: 'absolute',
            left: `${sign.position.x - pointSize.width / 2}px`,
            top: `${sign.position.y - pointSize.height / 2}px`,
          }}></div>
        {contentShown ? <AnnotationDetail
          class={['comment-dialog']}
          readonly={readonly}
          closeable={readonly}
          annotation={sign}
          onViewImages={(...args: any[]) => this.$emit('viewImages', ...args)}
          onChange={({ content, accessoryInfo: { images } }: Sign) => {
            sign.content = content;
            const accessoryInfo = sign.accessoryInfo ?? {};
            accessoryInfo.images = images;
            sign.accessoryInfo = accessoryInfo;
          }}
          onClose={() => { toggleComment(); }}
        /> : null}

      </div>
      {panel}
      {confirmPanel}
    </div>;
  },
});

export default AnnotationCreation;
