<template>
  <div style="width: 100%;">
    <div class="renderer-wrapper no-collapse">
      <div class="icons-menu">
        <el-tooltip :content="$t('model.preview.model_detail')">
          <i class="iconfont icon-help" @click="openShoeInfo" />
        </el-tooltip>
        <el-tooltip :content="$t('model.preview.3d_tag')" v-if="workspaceId">
          <i class="iconfont iconpizhu" @click="annotationsSidebarVisible = !annotationsSidebarVisible" />
        </el-tooltip>
      </div>

      <div
        class="flex-1"
        ref="rendererWrapper"
        :style="{
          position: 'relative',
          marginRight: annotationsSidebarVisible ? '460px' : '0'
        }"
        v-loading="loading"
      >
        <el-popover
          v-model="showStandardView"
          popper-class="standard-view-popper"
          placement="bottom-end"
          width="170"
          :visible-arrow="false"
          trigger="click"
        >
          <el-button slot="reference" circle class="standard-view-switch">
            <i class="iconfont  iconliushitu" />
          </el-button>
          <div class="standard-view-btn-wrap">
            <span class="standard-view-btn"></span>
            <span class="standard-view-btn"></span>
            <el-button
              plain
              class="standard-view-btn"
              :class="standardView === 'top' ? 'is-selected' : ''"
              @click="setStandardView('top')"
              >{{ $t("model.detail.standard_view.top") }}</el-button
            >
            <span class="standard-view-btn"></span>
            <el-button
              plain
              class="standard-view-btn"
              :class="standardView === 'back' ? 'is-selected' : ''"
              @click="setStandardView('back')"
              >{{ $t("model.detail.standard_view.back") }}</el-button
            >
            <el-button
              plain
              class="standard-view-btn"
              :class="standardView === 'left' ? 'is-selected' : ''"
              @click="setStandardView('left')"
              >{{ $t("model.detail.standard_view.left") }}</el-button
            >
            <el-button
              plain
              class="standard-view-btn"
              :class="standardView === 'front' ? 'is-selected' : ''"
              @click="setStandardView('front')"
              >{{ $t("model.detail.standard_view.front") }}</el-button
            >
            <el-button
              plain
              class="standard-view-btn"
              :class="standardView === 'right' ? 'is-selected' : ''"
              @click="setStandardView('right')"
              >{{ $t("model.detail.standard_view.right") }}</el-button
            >
            <span class="standard-view-btn"></span>
            <span class="standard-view-btn"></span>
            <el-button
              plain
              class="standard-view-btn"
              :class="standardView === 'bottom' ? 'is-selected' : ''"
              @click="setStandardView('bottom')"
              >{{ $t("model.detail.standard_view.bottom") }}</el-button
            >
            <span class="standard-view-btn"></span>
          </div>
        </el-popover>
        <!-- / -->
        <el-dropdown class="camera-type-dropdown" @command="changeCameraType" placement="bottom" trigger="click">
          <div class="camera-type-switch">
            <i class="iconfont" :class="currentCamera && currentCamera.icon"></i>
            <font>{{ currentCamera && currentCamera.name }}</font>
            <i class="el-icon-arrow-down"></i>
          </div>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item
              v-for="camera in cameraList"
              :key="camera.val"
              class="camera-type-item"
              :command="camera.val"
            >
              <i class="iconfont" :class="camera.icon"></i>
              <font>{{ camera.name }}</font>
              <i class="iconfont icon-tick" v-if="currentCamera && camera.val === currentCamera.val"></i>
              <i v-else></i>
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <canvas
          ref="renderer"
          style="width: 100%; height: 100%; position: absolute"
          @mousedown="standardView = ''"
          v-on="canvasEvent"
        />
        <!-- eslintdisable-next-line -->
        <!-- @ts-ignore -->
        <AnnotationCreation
          :key="signCurrent.id"
          style="left: 0; height: 100%; position: absolute; right: 0"
          v-if="signCurrent"
          class="annotation-creation-box"
          ref="annotationCreation"
          :readonly="true"
          @viewImages="viewImages"
          :sign="signCurrent"
        />
      </div>

      <AnnotationSidebar
        v-if="annotationsSidebarVisible && workspaceId"
        :catalogId="catalogId"
        :modelId="modelId"
        :workspaceId="workspaceId"
        modelName=""
        :sign="signCurrent"
        :activeId="signCurrent ? signCurrent.id : null"
        :readonly="true"
        :currentVersionOnly="true"
        class="annotation-layout"
        @annotation-display="({ annotation }) => showAnnotation(annotation)"
        @close="annotationsSidebarVisible = false"
      />
    </div>
  </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api';
import axios from '@/api/axios';
import i18n from '@/i18n';
import handBagInfo from '@/views/model/bag-info/index.vue';
import ModelInfo from '@/views/model/info';

import mixinRenderer from '@/views/model/mixin-renderer';

import { getWorkspaceConfigSelf } from '@/api/permission';
import { getModelCTMList, getAnnotationList } from '@/api/shoe';
import { getContextUser } from '@/loaders/context';

import { normalizeComponent } from '@/utils/normalize-component';
import { loadD4, loadD2 } from '@/utils/load-d4';
import AnnotationCreation from '../detail/annotation/AnnotationCreation';
import AnnotationSidebar from '../detail/annotation/index.vue';

async function getEnvSetting(shoeId, relateType) {
  return (
    await axios.get('/show/renderTask/envsetting/v2', {
      params: {
        shoeId,
        relateType,
      },
    })
  ).data;
}

export default defineComponent({
  name: 'model-preview',
  components: {
    AnnotationCreation,
    AnnotationSidebar,
  },
  props: {
    workspaceId: {
      default() {
        return this.$route.query.workspaceId;
      },
    },
    modelId: {
      default() {
        return this.$route.params.modelId;
      },
    },
    catalogId: {
      default() {
        return this.$route.query.catalogId;
      },
    },
    relateType: {
      default() {
        return this.$route.query.relateType || 0;
      },
    },
  },
  data() {
    return {
      loading: true,
      signCurrent: null,
      annotations: [],
      canvasEvent: null,
      annotation: null,
      annotationsSidebarVisible: this.$route.query.showAnnotation === 'true',
      showStandardView: false, // 
      standardView: '', // top back left front right bottom
      cameraList: [
        { name: i18n.t('model.detail.camera_mode_transparency'), val: 0, icon: 'icontoushimoshi-nor' },
        { name: i18n.t('model.detail.camera_mode_orthographic'), val: 1, icon: 'iconzhengjiaomoshi-nor' },
      ],
      currentCamera: null,
      envSetting: {}, // ...
    };
  },
  mixins: [mixinRenderer],
  watch: {
    async annotationsSidebarVisible(val, oldVal) {
      if (this.d4) this.renderAnnotationsInModel();
      if (this.d4) {
        if (val) {
          this.stashCameraType();
        } else {
          this.recoverCameraType();
        }
      }
    },
    signCurrent(val, oldVal) {
      if (val) {
        this.showStandardView = false;
      }
      this.standardView = '';
    },
  },
  methods: {
    viewImages(...args) {
      this.$ImageViewer.show(...args);
    },
    async openShoeInfo() {
      if (this.relateType === '3') {
        await this.$Dialog.dialogify({
          is: handBagInfo,
          props: {
            modelId: this.modelId,
            groupAppId: this.workspaceId,
            editable: false,
          },
        });
      } else {
        await this.$Dialog.dialogify({
          is: ModelInfo,
          props: {
            modelId: this.modelId,
            editable: false,
          },
        });
      }
    },
    async initD4() {
      const getModelData = async () => {
        const { data } = await getModelCTMList(this.modelId, this.workspaceId);
        // const { data } = await getModelCTMList(this.modelId);
        return Promise.all(data.map((item) => normalizeComponent(item)));
      };

      const [data, D4, D2, envSettingData] = await Promise.all([
        getModelData(),
        loadD4(),
        loadD2(),
        getEnvSetting(this.modelId, this.relateType),
      ]);

      // this.d4 = this.instanceD4(this.$refs.renderer);
      const { rendererWrapper } = this.$refs;
      const canvasDom = rendererWrapper.querySelector('canvas');
      const params = {
        canvas: canvasDom,
        width: rendererWrapper.clientWidth,
        height: rendererWrapper.clientHeight,
        resolution: window.devicePixelRatio,
        envSetting: envSettingData,
      };

      this.envSetting = envSettingData;
      this.d4 = new D4(params);
      const d2 = new D2();
      this.d2 = d2;
      this.canvasEvent = this._initCanvasEvent();
      this.envSetting.cameraJson = this.d4.outputCameraJson();
      const cameraJson = JSON.parse(this.envSetting.cameraJson);
      this.cameraTypeStashed = cameraJson.projection;
      this.changeCameraType(cameraJson.projection);

      const loadModel = async () => {
        await this.d4.loadModel(data);
      };

      const makeWaterMask = async () => {
        // workspace
        const openWatermark = this.workspaceId ? !!(await getWorkspaceConfigSelf(this.workspaceId)).data.openWatermark : false;

        if (this.workspaceId
          && (this.$can(this.workspaceId, 47) || openWatermark)) {
          // const res = await getWorkpaceCompnayInfo(this.workspaceId);
          const { mail, account } = getContextUser();
          await this.d4.updateWatermask(d2.getWatermask(mail || account));
        }
      };
      try {
        // eslint-disable-next-line
        await Promise.all([loadModel(), makeWaterMask()]);
      } catch (err) {
        console.log(err);
      } finally {
        this.loading = false;
      }
    },
    // 
    async fetchAnnotationList() {
      const versions = (await getAnnotationList(
        this.catalogId, this.modelId, this.workspaceId,
      )).data;
      this.annotations = versions
        .find(version => version.relateId === this.modelId)?.tdAnnotationResponseList ?? [];
    },
    // 
    async renderAnnotationsInModel() {
      if (this.annotationsSidebarVisible) {
        this.annotations.forEach(({ id }) => {
          try {
            this.d4.deleteSign(id);
          } catch (e) { }
        });
        await this.fetchAnnotationList();
        for (let idx = 0; idx < this.annotations.length; idx++) {
          const ann = this.annotations[idx];
          await this.d4.addSign(
            { ...ann.threeDimensionalInfo, id: ann.id },
            this.d2.getSignTexture(this.annotations.length - idx),
          );
        }
        this.d4.renderSigns();
      } else {
        this.signCurrent = null;
        this.d4.hideSigns();
      }
      await this.$nextTick();
      this.d4.resizeRenderer(
        this.$refs.rendererWrapper.clientWidth,
        this.$refs.rendererWrapper.clientHeight,
        window.devicePixelRatio,
      );
    },
    // 
    async showAnnotation(ann) {
      console.log('showAnnotation=============', ann);
      console.log(ann.id);
      ann.position = this.d4.getSignScreenPos(ann.id);
      if (!ann.position) {
        return;
      }
      console.log(ann.position);
      this.signCurrent = ann;
      // D4 1.8.109getSignScreenPoscameraType
      this.changeCameraType(ann.position.cameraType);
    },
    setStandardView(view) {
      this.standardView = view;
      this.signCurrent = null;
      // nextTick
      this.$nextTick(() => {
        this.standardView = view;
      });
      this.d4 && this.d4.setCameraViewAngle(view);
    },
    stashCameraType() {
      this.cameraTypeStashed = this.currentCamera.val;
    },
    recoverCameraType() {
      this.changeCameraType(this.cameraTypeStashed);
    },
    changeCameraType(type) {
      this.d4?.changeCameraType(type);
      this.currentCamera = this.cameraList.find(cam => cam.val === type);
    },
  },
  async mounted() {
    await this.initD4();
    this.renderAnnotationsInModel();
  },
});
</script>

