<template>
  <div class="annotation-container">
    <!--  -->
    <div v-if="!host" class="annotation-nav">
      <p>
        {{ $t('model.detail.annotation.tags') }}
      </p>
      <div>
        <i class="iconfont icon-delete" @click="hideAnnotationSidebar" />
      </div>
    </div>
    <!--  -->
    <scrollable y style="flex: 1" v-if="annotationVersions">
      <ul v-if="annotationVersions.length">
        <template v-for="version in annotationVersions">
          <li v-if="!currentVersionOnly || version.relateId === modelId" :key="version.relateId" class="annotation-version-item">
            <h3 class="version-title">
              {{ $t('model.detail.annotation.version') }} {{ version.vCode }}
            </h3>
            <ul v-if="version.tdAnnotationResponseList.length">
              <li
                @click="activateAnnotation(annotation, version.relateId, true)"
                v-for="annotation in version.tdAnnotationResponseList"
                :key="annotation.id"
                class="annotation-item"
                :class="{active: activeId === annotation.id}">
                <span
                  class="number"
                  v-if="version.relateId === modelId">
                  {{annotation.idx}}
                </span>
                <AnnotationItem
                  :annotation="annotation"
                  style="flex: 1">
                  <template v-if="!readonly" v-slot:tools>
                    <i
                      v-if="annotation.modifierInfo.id === user.id && version.relateId === modelId"
                      class="iconfont iconedit"
                      @click.stop="activateAnnotation(annotation, version.relateId, false)" />
                    <i
                      v-if="annotation.modifierInfo.id === user.id"
                      class="iconfont icon-file-delete"
                      @click.stop="removeAnnotation(version.tdAnnotationResponseList, annotation, version.relateId)" />
                  </template>

                </AnnotationItem>
              </li>
            </ul>
            <div class="annotation-empty" v-else>
              <div
                class="annotation-empty-creation"
                @click="$emit('creating-annotation')">
                <i class="iconfont iconbiaozhu-xianxing" />
                <span>
                  {{ $t('model.detail.annotation.create_tag') }}
                </span>
              </div>
            </div>
          </li>

        </template>
      </ul>
      <!--  -->
      <div v-else class="version-empty-annotation">
        <img src="@/assets/img/public/placeholder/placeholder-empty-notification.png" />
        <p class="placeholder-text">
          {{ $t('model.detail.annotation.no_tags_found') }}
        </p>
      </div>
    </scrollable>
    <div v-else style="flex: 1" v-loading="true"></div>
  </div>
</template>

<script>

import { getAnnotationList, deleteAnnotation } from '@/api/shoe';
import Scrollable from '@/components/Scrollable';

import { getHost } from '@/utils/exec';
import { getContextUser } from '@/loaders/context';
import AnnotationItem from './AnnotationItem';

export default {
  name: 'AnnotationSidebar',
  components: {
    AnnotationItem,
    Scrollable,
  },
  props: {
    readonly: {
      type: Boolean,
      required: false,
    },
    currentVersionOnly: {
      type: Boolean,
      required: false,
    },
    catalogId: {
      type: String,
      required: true,
    },
    workspaceId: {
      type: String,
      required: true,
    },
    modelId: {
      type: String,
      required: true,
    },
    modelName: {
      type: String,
      required: true,
    },
    activeId: {
      type: String,
    },
  },
  data() {
    return {
      versions: null,
      host: null,
    };
  },
  computed: {
    modelId_() {
      return this.modelId || this.versions[0]?.relateId;
    },
    /** @return { any } */
    annotationVersions() {
      if (!this.versions) return null;
      return this.versions.filter(v => {
        return v.tdAnnotationResponseList.length;
      });
    },
    /** @return { Item<User> } */
    user() {
      return getContextUser();
    },
  },
  async created() {
    this.fetchAnnotationList();
    this.host = await getHost();
  },
  mounted() {
    this.$root.$on('add-annotation', this.fetchAnnotationList);
  },
  methods: {
    // 
    hideAnnotationSidebar() {
      this.$emit('close');
    },
    async removeAnnotation(annotations, ann, versionId) {
      // 
      annotations.splice(annotations.findIndex(annotation => annotation.id === ann.id), 1);
      await deleteAnnotation(ann.id, this.workspaceId, versionId, this.catalogId, this.modelName);
      this.$emit('annotation-remove', ann);
    },
    activateAnnotation(annotation, modelId, readonly) {
      if (modelId === this.modelId) {
        this.$emit('annotation-display', {
          annotation: JSON.parse(JSON.stringify(annotation)), modelId, readonly,
        });
        // this.host.actAnnotationOperation({ action: 'annotation-display', params: ann });
      } else {
        this.$emit('model-preview', {
          workspaceId: this.workspaceId,
          catalogId: this.catalogId,
          annotation,
          modelId,
          readonly,
        });
        // eslint-disable-next-line
        // this.host.actAnnotationOperation({ action: 'model-preview', params: modelId });
      }
    },
    async fetchAnnotationList() {
      const versions = (await getAnnotationList(
        this.catalogId, undefined, this.workspaceId,
      )).data;
      versions.forEach(version => {
        version.tdAnnotationResponseList.forEach((ann, idx) => {
          ann.threeDimensionalInfo.id = ann.id;
          ann.idx = version.tdAnnotationResponseList.length - idx;
        });
      });
      this.$emit('load-annotations', versions);
      this.versions = versions;
    },
  },
};
</script>

